@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html,
body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #121212;
  color: #fff;
}

.main {
  padding-left: 5rem;
}

.font-header {
  font-family: "Lato";
}

.font-primary {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.align-screen--vertical-centered {
  display: flex;
  align-items: center;
  height: 100%;
}

.align-screen--centered {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.icon {
  fill: #fff;
  opacity: 0.8;
  width: 20px;
  height: 20px;
}

.icon:hover {
  fill: #2be2ff;
  opacity: 1;
}

@media screen and (max-width: 480px) {
  .main {
    padding-left: 2rem;
  }
}

@media screen and (max-width: 400px) {
  .main {
    padding-left: 1rem;
  }
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

