.loader-in-progress {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 200000;
}

.loader-spinner {
  background-color: rgba(0, 0, 0, 0);
  border: 4px solid #2be2ff;
  opacity: 0.9;
  border-left: 5px solid rgba(0, 0, 0, 0);
  border-radius: 120px;
  /* -webkit-box-shadow: 0 0 5px #2be2ff; */
  /* box-shadow: 0 0 5px #2be2ff; */
  width: 30px;
  height: 30px;
  margin: 0 auto;
  animation: spin 0.5s infinite linear;
}

.loader-txt {
  margin-top: 1rem;
  font-size: .75rem;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.menu {
  display: flex;
  flex-direction: column;
  position: fixed; top: 0; right: 0;
  padding: 1rem;
  margin: 1rem;
  z-index: 2;
  /* background: #232323;
  border-radius: 40px; */
}

.menu .icon {
  width: 30px;
  height: 30px;
  opacity: 0.5;
}

.menu a {
  text-align: center;
}

.menu a:not(:last-child) {
  margin-bottom: 1.25rem;
}

.menu .icon:hover {
  opacity: 1;
}

.menu .active .icon {
  fill: #2be2ff;
  opacity: 1;
}

.link--disabled {
  pointer-events: none;
  opacity: 0.3;
}

@media screen and (max-width: 640px) {
  .menu {
    flex-direction: row;
    justify-content: space-evenly;
    position: fixed;
    top: auto;
    top: initial;
    bottom: 0;
    width: 100%;
    padding: 0;
    margin: 0;
  }
}
